.signin-outer {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-image: url("../../../assets/img/background.jpg");
}

.card-signin {
  border-radius: 40px;
  width: 100%;
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: 390px;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  background-color: #ffffffbf;
  padding: 50px 8px !important;
  height: 400px;
}

.btn {
  border-radius: 7px;
  height: 38px;
  width: 100%;
  background-color: #fd6400;
  box-shadow: none;
}

.card-body {
  margin: 0 auto;
  width: 72%;
}

.w-75 {
  width: 480px !important;
}

.info {
  display: flex;
  color: #3e3e3e;
  margin-top: 16px;
  font-size: 14px;
  height: 88px;
  align-items: center;
  justify-content: center;
}

.btn1 {
  color: #000;
  background-color: #fff;
  border: solid 0.4px #979797 !important;
  margin: 0 10px 0 0;
}

.verify {
  color: green;
  font-size: 20px;
  font-family: "gothambook", sans-serif;
  font-weight: bolder;
}

.notverify {
  color: red;
  font-size: 15px;
  font-family: "gothambook", sans-serif;
}